//Hero
.scene-banner{
    background-image: url(../images/scene/banner-scene.jpg);
}
//Main
.scene-image{
    width: 100%;
    height: 200px;
    background-repeat: no-repeat;
    background-position: bottom center;
    background-size: cover;
}
.map{
    width: 100%;
    height: 400px;
    border: 2px solid $white;
    border-radius: 8px;
}
.scene-info p{
    margin-bottom: 16px;
    text-indent:2em;
}
@media(min-width:768px){
    .scene-info{
        min-height: 400px;
        padding-right: 24px;
    }
    .map{
        height: 100%;
    }
}
